.form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 400px;
    height: auto;
    position:fixed;
