    border-radius: 10px;
}
.form__row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    width: 100%;
    padding: 20px 20px 0px 20px;
}
.form__input {
    width: 100%;
    padding: 15px 20px;
    border-radius: 5px 0 0 5px;
    border: 1px solid #ddd;
    box-sizing: border-box;
    outline: 0;
}
.form__input:focus {
    background-color: rgba(245, 243, 243, 0.5);
}
.form__button {

    background-color: #000;
    color: #fff;
    border: 0;
    border-radius: 0 5px 5px 0;
